<template>
  <div class="sider">
    <div class="logo">LOGO</div>
    <div class="menu">
      <div class="menu-item" v-for="item in Navs" to="item.router">
        <span>icon</span>
        {{item.title}}
        <span> ></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Navs: [
        {title: '证件照', router: '/photo', name: ''},
        {title: '标题二', router: '/', name: ''},
        {title: '标题三', router: '/', name: ''},
      ]
    }
  }
}
</script>

<style>
.sider{
  position: fixed;
  z-index: 99;
  top: 0px;
  width: 145px;
  height: 100%;
  background-color: rgb(40, 43, 47);
}
.sider .logo {
  width: 145px;
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  font-weight: 400;
  /* padding-left: 20px; */
  color: #fff;
  background-color: rgb(29,29,38);
}
.sider .menu {
  color: aliceblue;
}
.menu-item {
  height: 38px;
  line-height: 38px;
  font-size: 12px;
  padding-left: 16px;
  border-bottom: 1px solid rgb(75, 75, 75);
}
</style>



